export default {
	name: 'xlist-input',
	data() {
		return {
			inputValue: ''
		}
	},
	props: {
		title: {
			type: String,
			default: '姓名：'
		},
		placeholder: {
			type: String,
			default: '请输入您的姓名'
		},
		typeStyle: {
			type: String,
			default: 'text'
		}
	},
	methods: {
		bindInputValue(e){
			let inputValue = e.detail.value
			this.$emit('inputValue', inputValue)
		}
	}
}
